.header-wrapper {
    height: 100vh;
    padding-top: 3rem;
    overflow: hidden;

    .header-container {
        text-align: center;
        height: 100%;
        position: relative;

        .me {
            background: linear-gradient($clr-primary, transparent);
            width: 22rem;
            height: 30rem;
            position: absolute;
            left: calc(50% - 11rem);
            margin-top: 4rem;
            border-radius: 12rem 12rem 0 0;
            overflow: hidden;
            // padding: 5rem 1.5rem 1.5rem 1.5rem;

            img {
                object-position: top;
            }
        }


        .scroll-down {
            position: absolute;
            right: -2.3rem;
            bottom: 5rem;
            transform: rotate(90deg);
            font-weight: 300;
            font-size: 0.9rem;
        }


    }
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header-socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;

    &::after {
        content: '';
        width: 1px;
        height: 2rem;
        background-color: $clr-primary;
    }
}

@media screen and (max-width: 600px) {
    .header-wrapper {
        .scroll-down {
            display: none;
        }
    }

    .header-socials {
        display: none;
    }
}